import React from "react";
import logo from "./logo.svg";
import "./App.css";
import "emerald-ui/lib/styles.css";
import Table from "emerald-ui/lib/Table";
import Container from "emerald-ui/lib/Container";
import Row from "emerald-ui/lib/Row";
import Col from "emerald-ui/lib/Col";
import { useCallback, useEffect, useState } from "react";
import axios from "axios";
import Spinner from "emerald-ui/lib/Spinner";
import Button from "emerald-ui/lib/Button";
import PageHeader from "emerald-ui/lib/PageHeader";
import TextField from "emerald-ui/lib/TextField";

function App() {
  const [items, setItems] = useState([]);

  const [pages, setPages] = useState(0);
  const [current_page, setCurrentPage] = useState(0);
  const [size, setSize] = useState(0);

  const [next, setNext] = useState(null);
  const [back, setBack] = useState(null);

  const [fullname, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [city, setCity] = useState("");

  const [loading, setLoading] = useState(false);

  const load = async (
    url = "http://localhost:5000/api/v1/users?offset=0&limit=10"
  ) => {
    try {
      setSize(0);
      setNext(null);
      setBack(null);
      setLoading(true);
      const request = await axios.get(url);
      setItems(request.data.items);
      setLoading(false);

      if (request.data.pages) {
        setPages(request.data.pages);
      }

      if (request.data.current_page) {
        setCurrentPage(request.data.current_page);
      }

      if (request.data.next) {
        setNext(request.data.next);
      }
      if (request.data.size) {
        setSize(request.data.size);
      }

      if (request.data.previus) {
        setBack(request.data.previus);
      }
    } catch (error) {
      setLoading(false);
    }
  };
  const loadCallback = useCallback(load, []);

  useEffect(() => {
    loadCallback();
  }, []);

  const paginate = (a) => {
    if (a < 0) {
      load(back);
    } else if (a > 0) {
      load(next);
    }
  };

  const filter = () => {
    let URL = "http://localhost:5000/api/v1/users?offset=0&limit=10";
    if (fullname != "") {
      URL += `&fullname=${fullname}`;
    }
    if (city != "") {
      URL += `&city=${city}`;
    }
    if (email != "") {
      URL += `&email=${email}`;
    }
    load(URL);
  };

  const renderItems = () => {
    return items.map((item) => (
      <tr key={item._id}>
        <td>{item.fullname}</td>
        <td>{item.city}</td>
        <td>{item.email}</td>
      </tr>
    ));
  };

  return (
    <React.Fragment>
      <PageHeader>
        <ol className="breadcrumb">
          <li>
            <a href="https://github.com/condorevega" target="blank">
              <span className="chevron-back"></span>
              Evaldo Vega Vivanco
            </a>
          </li>
        </ol>
        <h1>Backend Basic</h1>
      </PageHeader>

      <Container>
        <p></p>

        <Row>
          <Col xs={2} className="sample-col"></Col>
          <Col xs={8} className="sample-col">
            {loading && (
              <Spinner style={{ margin: "0 auto", display: "block" }} />
            )}
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <TextField
                style={{ marginTop: 0 }}
                onChange={(v) => setFullName(v.target.value)}
                label="Fullname"
              />
              <TextField
                style={{ marginTop: 0 }}
                onChange={(v) => setCity(v.target.value)}
                label="City"
              />
              <TextField
                style={{ marginTop: 0 }}
                onChange={(v) => setEmail(v.target.value)}
                label="Email"
              />
              <Button onClick={() => filter()}>Search</Button>
            </div>
            <Table>
              <thead>
                <tr>
                  <th>Fullname</th>
                  <th>City</th>
                  <th>Email</th>
                </tr>
              </thead>
              <tbody>{renderItems()}</tbody>
            </Table>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                marginTop: 16,
              }}
            >
              <Button disabled={!back} onClick={() => paginate(-1)}>
                Previus
              </Button>
              <span>
                {current_page} / {pages}
              </span>
              <Button disabled={!next} onClick={() => paginate(1)}>
                Next
              </Button>
              <span>
                Users <b>{size}</b>
              </span>
            </div>
          </Col>
          <Col xs={2} className="sample-col"></Col>
        </Row>
      </Container>
    </React.Fragment>
  );
}

export default App;
